import "./App.css";
import React from "react";
import Navbar from "./component/Navbar";
// import Textform from './component/Textform'
// import About from "./component/About";
import { useState } from "react";
import Alert from "./component/Alert";
import Textform from "./component/Textform";


// import { BrowserRouter as Router, Route, Routes } from 'react-router-dom'

/* eslint-disable jsx-a11y/anchor-is-valid */
function App() {
  const [mode, setmode] = useState("light");
  const [alert, setalert] = useState(null);

  const showalert = (message, type) => {
    setalert({
      msg: message,
      type: type,
    });
    setTimeout(() => {
      setalert(null);
    }, 3000);
  };

  const togglemode = () => {
    if (mode === "light") {
      setmode("dark");
      document.body.style.backgroundColor = "grey";
      showalert("dark mode enabled", "success");
      // document.title='TextUtils-lightmode';
      // setInterval(() => {
      //   document.title = "TextUtils-mc";
      // }, 2000);
      // setInterval(() => {
      //   document.title = "huehue";
      // }, 1500);
    } else {
      setmode("light");
      document.body.style.backgroundColor = "white";
      showalert("light mode enabled", "success");
      document.title = "TextUtils-darkmode";
    }
  };
  return (
    <>
{/* <Router> */}
      {/* <Navbar /> */}

        <Navbar title="hehe" name="about" mode={mode} togglemode={togglemode} />
        <Alert alert={alert} />
        <div className="container">
          {/* <Routes>
          <Route exact path="/about" element={<About mode={mode}/>}/> */}
          {/* <Route exact path="/" element={ */}
          {/* <Textform showalert={showalert} heading="Enter text" /> */}
          {/* }/> */}

          {/* <Route exact path="/about" element={<About mode={mode}/>}> */}
          {/* < exact path="/" element={<Textform showalert={showalert} heading="Enter text" />}/> */}

          <Textform showalert={showalert} heading="Enter text" />



          {/* </Routes> */}

          {/* <About/> */}
        </div>
        {/* </Router> */}
    </>
  );
}

export default App;
